.MainInformation {
    h2 {
        font-size: 51px;
        font-weight: 700;
        margin: 0 0 30px 0;
    }

    h2::after {
        content: "";
        display: inline-block;
        width: 12px;
        height: 12px;
        background: #00d0ff;
        border-radius: 50%;
        margin: 0 0 0 15px;
    }

    .infor_wrapper {
        display: flex;
        gap: 30px;

        .left {
            flex: 1;
            border: 1px solid #eee;
            padding: 20px 30px;
            border-radius: 20px;
        }

        .right {
            flex: 1;
            border: 1px solid #eee;
        }

        .notice_wrapper {
            display: flex;
            justify-content: space-between;
        }

        .notice {
            display: block;
            font-size: 21px;
            font-weight: 700;
            margin: 0 0 10px 0;
            line-height: 40px;
            // border-bottom: 1px solid #eee;
        }

        .notice_icon {
            font-size: 21px;
            color: #777;
            line-height: 40px;
        }

        .notice_wrapper:hover .notice_icon {
            color: #00d0ff;
            transition: 0.5s;
        }

        li {
            line-height: 40px;
            display: flex;
            justify-content: space-between;
        }

        li:hover {
            color: #777;
            transition: 0.5s;
        }
        
    }
}